import React, { useEffect, useState } from 'react';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import firebase from '../../firebase'
import { MenuItem, Select, FormControl } from "@material-ui/core";
import { makeStyles } from '@material-ui/core/styles';
import InputLabel from '@material-ui/core/InputLabel';
const useStyles = makeStyles((theme) => ({
    formControl: {
        margin: theme.spacing(1),
        minWidth: 120,
    },
    selectEmpty: {
        marginTop: theme.spacing(2),
    },
}));
export default function FormDialog(props) {
    const [open, setOpen] = React.useState(false);

    const classes = useStyles();

    useEffect(() => {
        handleClickOpen()
    }, [])
    const handleClickOpen = () => {
        setOpen(true);

    };

    const handleClose = () => {
        setOpen(false);
        props.closemodle()

    };

    const [name, setname] = useState('')
    const [email, setemail] = useState('')
    const [phone, setphone] = useState('')
    const [message, setmessage] = useState(0)
    const [error, seterror] = useState(true)

    const namehandler = (e) => {
        setname(e.target.value)
    }

    const emailhandler = (e) => {
        setemail(e.target.value)
    }
    const phonehandler = (e) => {
        setphone(e.target.value)
    }
    const selecthandler = (e) => {
        setmessage(e.target.value)
    }

    const datadump = async () => {

        if (name === '' || email === '' || phone === '') {
            seterror('field is empty')

        } else {
            if (phone.length > 10 || phone.length < 10) {
                seterror('phone no. is invalid please enter valid 10 digit no.')
            }
            else {
                if (props.type === 'Student') {
                    if (message > 0) {
                        const db = firebase.firestore()
                        if (props.type === 'Student') {
                            const data = await db.collection('studentform').add({ timestamp: Date.now(), name: name, phone: phone, address: email, class: message }).then(() => {
                                handleClose()
                            })
                        }
                        else {
                            const data = await db.collection('tutorform').add({ timestamp: Date.now(), name: name, phone: phone, address: email }).then(() => {
                                handleClose()
                            })
                        }
                    }
                    else {
                        seterror('please select class')
                    }

                }
                else {

                    const db = firebase.firestore()
                    if (props.type === 'Student') {
                        const data = await db.collection('studentform').add({ timestamp: Date.now(), name: name, phone: phone, address: email, class: message }).then(() => {
                            handleClose()
                        })
                    }
                    else {
                        const data = await db.collection('tutorform').add({ timestamp: Date.now(), name: name, phone: phone, address: email }).then(() => {
                            handleClose()
                        })
                    }
                }




            }
        }



    }
    return (
        <div>

            <Dialog open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
                <DialogTitle id="form-dialog-title">{props.type === 'tutor' ? 'Become A Tutor' : 'Please Fill the Following Details'}</DialogTitle>
                <DialogContent>

                    <TextField
                        autoFocus
                        margin="dense"
                        id="name"
                        label="Name "
                        type="text"
                        fullWidth
                        onChange={namehandler}
                        variant="outlined"
                    />
                    <TextField

                        margin="dense"
                        id="Phone"
                        label="Phone no."
                        type="number"
                        fullWidth
                        onChange={phonehandler}
                        variant="outlined"
                    />
                    <TextField

                        margin="dense"
                        id="emil"
                        label="Address"
                        type="text"
                        fullWidth
                        onChange={emailhandler}
                        variant="outlined"
                        multiline
                    />

                    {props.type === 'Student' ? <FormControl variant="outlined" className={classes.formControl}>
                        <InputLabel id="demo-simple-select-filled-label">Select Class</InputLabel>
                        <Select
                            labelId="demo-simple-select-filled-label"
                            id="demo-simple-select-filled"
                            value={message}
                            onChange={selecthandler}
                            label="Select Class"
                        >
                            <MenuItem value={1}>1<sup>st</sup></MenuItem>
                            <MenuItem value={2}>2<sup>nd</sup></MenuItem>
                            <MenuItem value={3}>3<sup>rd</sup></MenuItem>
                            <MenuItem value={4}>4<sup>th</sup></MenuItem>
                            <MenuItem value={5}>5<sup>th</sup></MenuItem>
                            <MenuItem value={6}>6<sup>th</sup></MenuItem>
                            <MenuItem value={7}>7<sup>th</sup></MenuItem>
                            <MenuItem value={8}>8<sup>th</sup></MenuItem>
                            <MenuItem value={9}>9<sup>th</sup></MenuItem>
                            <MenuItem value={10}>10<sup>th</sup></MenuItem>
                            <MenuItem value={11}>11<sup>th</sup></MenuItem>
                            <MenuItem value={12}>12<sup>th</sup></MenuItem>
                        </Select>
                    </FormControl> : null}
                </DialogContent>
                <DialogActions>

                    <Button onClick={datadump} color="primary">
                        Submit
          </Button>
                </DialogActions>

                {error ? <label style={{ color: 'red' }}>{error}</label> : null}
            </Dialog>
        </div >
    );
}
