import React, { useState } from 'react'
import { Container, Row, Col, Button } from 'react-bootstrap'
import { Link } from 'react-scroll'

import InstagramIcon from '@material-ui/icons/Instagram';
import FacebookIcon from '@material-ui/icons/Facebook';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import Fomi from '../form/form'


const Footer = () => {

    const [modle, setmodel] = useState(false)
    const [type, settype] = useState('Student')
    const openmodel = (ty) => {
        settype(ty)
        setmodel(true)
    }
    const closemodel = () => {
        setmodel(false)
    }
    return (

        <section id='Foot' className='container-fluid p-5' lg={12} style={{ backgroundColor: '#152238' }} >
            <Row className='d-flex flex-lg-row flex-column ' >
                <Col lg={12} xs={12} md={12} className='m-2' className='d-flex justify-content-space-between flex-column align-items-center
                pl-lg-5 pr-lg-5' >

                    <Link to='Home' style={{ color: 'lightgray', pointer: 'cursor' }}
                        smooth={true} duration={500} spy={true} >Home</Link>


                    <Link to='Fu' style={{ color: 'lightgray', cursor: 'pointer' }}
                        smooth={true} duration={500} spy={true} >Why Homeguruji?</Link>

                    <Link to='Exp' style={{ color: 'lightgray', cursor: 'pointer' }}
                        smooth={true} duration={500} spy={true} >Experiences</Link>

                    <div className='p-2'>
                        <Button className='m-2' onClick={() => openmodel('Student')}
                        >Start your Trial</Button>

                        <Button className='m-2' onClick={() => openmodel('tutor')}
                        >Become a tutor</Button>

                    </div>




                </Col>
                <Col className='d-flex justify-content-center align-item-center mt-4 mb-4 ' lg={12} xs={12} md={12} style={{ alignItems: 'center' }}>
                    <InstagramIcon style={{ color: 'white' }} />
                    <FacebookIcon style={{ color: 'white' }} />
                    <LinkedInIcon style={{ color: 'white' }} />
                </Col>

                <Col lg={12} xs={12} md={12} className='m-2'>

                    <h3 style={{ fontSize: '2.5vh', color: 'white', fontWeight: 'normal' }} className='text-lg-center text-sm-center  '>
                        Contact Us
                    </h3>
                    <h5 className='text-lg-center text-sm-center' style={{ color: 'lightgray', fontSize: '2vh', fontWeight: 'lighter' }}>
                        Phone no. +91 8103681802
                        <br />
                        Address:
3 floor, JIC, Udhyog Bhawan, Beside Katanga TV Tower Jabalpur, Madhya Pradesh 482002 India
                         <br />

                    </h5>
                </Col>
            </Row>


            <h5 style={{ fontSize: '2vh', color: 'white', fontWeight: 'lighter', marginTop: '10px' }}>© copyright and developed by Dave's Brother Technology</h5>
            {modle ? <Fomi type={type} closemodle={closemodel} /> : null}
        </section >

    )
}
export default Footer 