import React, { useState } from 'react';
import Carousel from 'nuka-carousel';
import { Container, Button, Col } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css'
import Divi from './data'
import Fomi from '../form/form'


const Experience = () => {

    const [modle, setmodel] = useState(false)
    const openmodel = () => {
        setmodel(true)
    }
    const closemodel = () => {
        setmodel(false)
    }
    return (
        <section className='container-fluid mt-5 p-3' id='Exp'>
            <h2 className=' ubu'>The Homeguruji Experience </h2>


            <Carousel
                className='mt-5'
                slidesToScroll={1} slidesToShow={window.innerWidth <= 760 ? 1 : 3} transitionMode='scroll' defaultControlsConfig={

                    {

                        nextButtonStyle: {
                            color: 'gray',
                            borderRadius: '200px',
                            backgroundColor: 'white',
                            height: '30px',
                            width: '30px',
                            alignItems: 'center',
                            boxShadow: '0px 0px 7px gray',
                            padding: '0px'
                        },
                        nextButtonText: '>',
                        prevButtonStyle: {
                            color: 'gray',
                            borderRadius: '200px',
                            backgroundColor: 'white',
                            height: '30px',
                            width: '30px',
                            alignItems: 'center',
                            boxShadow: '0px 0px 7px gray',
                            padding: '0px'
                        },
                        prevButtonText: '<',
                        pagingDotsStyle: {
                            display: 'none'
                        }
                    }
                }>

                <Divi bg='lightpink' textcolor='darkpink' no='t1' />
                <Divi bg='lightgreen' textcolor='darkgreen' no='t2' />
                <Divi bg='lightblue' textcolor='darkblue' no='t3' />
                {/*  <Divi bg='lightgray' textcolor='black' />
                <Divi bg='orange' textcolor='darkred' />
                <Divi bg='lightgreen' textcolor='darkgreen' />
                <Divi bg='lightsteelblue' textcolor='darksteelblue' />
            <Divi bg='lightpink' textcolor='darkpink' />*/}





            </Carousel>

            <Carousel
                className='mt-5'
                slidesToScroll={1} slidesToShow={window.innerWidth <= 760 ? 1 : 3} transitionMode='scroll' defaultControlsConfig={

                    {

                        nextButtonStyle: {
                            color: 'gray',
                            borderRadius: '200px',
                            backgroundColor: 'white',
                            height: '30px',
                            width: '30px',
                            alignItems: 'center',
                            boxShadow: '0px 0px 7px gray',
                            padding: '0px'
                        },
                        nextButtonText: '>',
                        prevButtonStyle: {
                            color: 'gray',
                            borderRadius: '200px',
                            backgroundColor: 'white',
                            height: '30px',
                            width: '30px',
                            alignItems: 'center',
                            boxShadow: '0px 0px 7px gray',
                            padding: '0px'
                        },
                        prevButtonText: '<',
                        pagingDotsStyle: {
                            display: 'none'
                        }
                    }
                }>

                <Divi bg='lightpink' textcolor='darkpink' no='s1' />
                <Divi bg='lightgreen' textcolor='darkgreen' no='s2' />
                <Divi bg='lightblue' textcolor='darkblue' no='s3' />
                <Divi bg='lightsteelblue' textcolor='darksteelblue' no='s4' />
                <Divi bg='orange' textcolor='darkred' no='s5' />

                {/*<Divi bg='lightgray' textcolor='black' />
                <Divi bg='orange' textcolor='darkred' />
                <Divi bg='lightgreen' textcolor='darkgreen' />
                <Divi bg='lightsteelblue' textcolor='darksteelblue' />
            <Divi bg='lightpink' textcolor='darkpink' />*/}





            </Carousel>

            <div className='mt-3'>
                <Button style={{ borderRadius: '20px' }} className='pl-4  pr-4 shadow-lg'
                    onClick={openmodel}
                >
                    Book Now
                        </Button>
            </div>
            {modle ? <Fomi type='Student' closemodle={closemodel} /> : null}
        </section>
    );
}

export default Experience