import React, { useState } from 'react'
import {
    Navbar, Nav, NavDropdown, Form,
    FormControl, Button
} from "react-bootstrap";
import { Link } from 'react-scroll'
import plasses from './navigation.module.css'
import Logo from '../../assets/homegurujilogo.jpg'
import 'bootstrap/dist/css/bootstrap.min.css'
import Fomi from '../form/form'

const Navigation = () => {
    const [expanded, setexpanded] = useState(false)
    const [modle, setmodel] = useState(false)
    const [type, settype] = useState('student')
    const openmodel = (ty) => {
        settype(ty)
        setmodel(true)
    }
    const closemodel = () => {
        setmodel(false)

    }


    const setexpnded = () => {

        setexpanded(false)
        toggle(true)
    }
    const toggle = (exp) => {
        setexpanded(!exp)
    }

    return (

        <Navbar collapseOnSelect bg="white" onToggle={() => toggle(expanded)} expand="lg" className='container-fluid justify-content-space-between shadow-sm ' fixed='top' expanded={expanded} >
            <Navbar.Brand as={Link} to='Home' ><img

                src={Logo}
                width="60"
                height="60"
                className="d-inline-block align-top"
                alt="homeguruji logo"
            />


            </Navbar.Brand>
            <div className='d-flex justify-content-center d-lg-none bg-primary '>


                <Button onClick={() => openmodel('Student')}

                >Book Now</Button>
            </div>

            <Navbar.Toggle aria-controls="responsive-navbar-nav" />


            <Navbar.Collapse id="responsive-navbar-nav" >

                <Nav className="ml-auto" >
                    <Nav.Link as={Link} activeClass={plasses.activelink} to="Home" onClick={() => (setexpanded(false))}
                        className={plasses.Lik} style={{ fontWeight: 'bold' }} smooth={true} duration={500} > Home </Nav.Link >
                    <Nav.Link as={Link} activeClass={plasses.activelink} to="Fu" onClick={() => (setexpanded(false))}
                        className={plasses.Lik} smooth={true} style={{ fontWeight: 'bold' }} duration={500} >Why Homeguruji? </Nav.Link >
                    <Nav.Link as={Link} activeClass={plasses.activelink} to="Vedio" onClick={() => (setexpanded(false))}
                        className={plasses.Lik} smooth={true} style={{ fontWeight: 'bold' }} duration={500} >About Us</Nav.Link >
                    <Nav.Link as={Link} activeClass={plasses.activelink} to="Exp" onClick={() => (setexpanded(false))}
                        className={plasses.Lik} smooth={true} style={{ fontWeight: 'bold' }} duration={500} >Testimonials</Nav.Link >
                    <Nav.Link as={Link} activeClass={plasses.activelink} className={plasses.Lik} to="Foot" onClick={() => (setexpanded(false))}
                        smooth={true} duration={500} style={{ fontWeight: 'bold' }}  >Contact Us</Nav.Link >

                    <Button style={{ backgroundColor: 'transparent', border: 'none', color: 'gray' }}
                        onClick={() => openmodel('tutor')}
                    >Become a Tutor </Button>

                </Nav>


                {console.log(expanded)}
            </Navbar.Collapse>
            {console.log(type)}
            {modle ? <Fomi type={type} closemodle={closemodel} /> : null}
        </Navbar>
    )
}
export default Navigation