
import React, { useState } from 'react'
import { Container, Row, Col, Button } from 'react-bootstrap'
import 'bootstrap/dist/css/bootstrap.min.css'
import './homepage.css'
import homeimg from '../../assets/8576.jpg'
import { ArrowDownCircleFill } from 'react-bootstrap-icons'
import { Link } from 'react-scroll'
import Fomi from '../form/form'


const Homepage = () => {

    const [modle, setmodel] = useState(false)
    const openmodel = () => {
        setmodel(true)
    }
    const closemodel = () => {
        setmodel(false)
    }
    return (
        <section id='Home' className='container-fluid mh-100 pt-5 ' >


            <Container
                className='container-fluid nav-bg p-3'
                style={{ height: 'auto' }}>
                <Row className='d-flex flex-md-row-reverse' >

                    <Col xs={12} lg={6} md={12}
                        className='float-md-right mt-5 h-sm-25 h-md-100'>
                        <img src={homeimg} className='img-fluid App-logo ' />

                    </Col>

                    <Col className=' d-flex flex-column mt-5 justify-content-lg-center'>
                        <h1
                            style={{ fontSize: '7vh' }}
                            className='display-2 ubu
                             text-center 
                             text-md-left ' >
                            Live 1:1 Home Tuition classes
                            for class 1<sup>st</sup>-12<sup>th</sup>
                        </h1>

                        <h8 style={{ fontSize: '3vh', fontWeight: "normal" }}
                            className='display-4 smtext
                            text-center
                            text-lg-left '>
                            Be a Limitless Learner wth Homeguruji
                        </h8>
                        <div className='d-flex  justify-content-md-start justify-content-center  '  >
                            <Button
                                onClick={openmodel}
                                className='mt-4 
                                float-center
                                float-md-left
                                shadow-lg
                                bg-primary 
                                float-center
                                pl-3 pr-3 
                                '
                            >Book Now</Button>
                        </div>
                        <Row sm={12} lg={4} md={4} className=' d-flex flex-row mt-4 justify-content-around mr-lg-5 '>
                            <div className='smallcard'>
                                All Boards
                                  </div>
                            <div className='smallcard'>
                                LIVE classes
                                  </div>
                            <div className='smallcard'>
                                Home Tuition
                                  </div>





                        </Row>

                    </Col>



                </Row>

                <ArrowDownCircleFill as={Link} to='aboutus' onClick={() => (console.log('iconclicked'))}
                    className='mt-5 animatearrow'
                    color="royalblue" size={36} />


            </Container>

            {modle ? <Fomi type='Student' closemodle={closemodel} /> : null}
        </section>
    )
}
export default Homepage