
import React, { useState } from 'react'
import { Container, Row, Col, Button } from 'react-bootstrap'
import 'bootstrap/dist/css/bootstrap.min.css'
import hg from '../../assets/hg.mp4'

import YouTubePlayer from 'react-player';

import homeimg from '../../assets/bk.jpg'
import Fomi from '../form/form'

const Vedio = () => {

    const [modle, setmodel] = useState(false)
    const openmodel = () => {
        setmodel(true)
    }
    const closemodel = () => {
        setmodel(false)
    }
    return (
        <section id='Vedio' className='container-fluid  mt-5 bg-primary ' style={{
            backgroundImage: `url("${homeimg}")`, backgroundPosition: 'center',
            backgroundRepeat: 'no-repeat', backgroundSize: 'cover'
        }} >
            <Container
                className='container-fluid nav-bg p-3'
                style={{ height: 'auto' }}>
                <h1
                    style={{ fontSize: '5vh' }}
                    className='display-2 ubu
                             text-center mt-4 ' >
                    About Us
                            </h1>

                <Row className='d-flex flex-column mt-5   justify-content-center '  >
                    <Col xs={12} lg={12} md={12} className=' d-flex justify-content-center'

                    >

                        <YouTubePlayer controls
                            url={hg}
                        />
                    </Col>

                    <Col xs={12} md={12} lg={12}
                        className='d-flex justify-content-center '>
                        <div className='d-flex flex-column  justify-content-center'   >

                            <h3
                                style={{ fontSize: '3vh' }}
                                className='d ubu
                             text-center mt-4 ' >
                                Try Before you Buy!
                            </h3>

                            <h5
                                style={{ fontSize: '3vh' }}
                                className='display-2 sans
                             text-center mt-4 ' >
                                Instantly book a free trial for your little
                                one. Empower them to express!
                    </h5>

                            <div className='d-flex  justify-content-center  '  >
                                <Button
                                    onClick={openmodel}
                                    style={{ borderRadius: '30px' }}
                                    className='mt-4 
                                float-center
                                float-md-left
                               
                                bg-primary 
                                float-center
                                pl-3 pr-3 
                                '
                                >Start yout trial</Button>
                            </div>

                        </div>

                    </Col>
                </Row>




            </Container>

            {modle ? <Fomi type='Student' closemodle={closemodel} /> : null}
        </section>
    )
}
export default Vedio