import React from 'react'
import { Container, Col } from 'react-bootstrap'
import Reactplayer from 'react-player'
import t1 from '../../assets/t1.mp4'
import t2 from '../../assets/t2.mp4'
import t3 from '../../assets/t3.mp4'
import s1 from '../../assets/s1.mp4'
import s2 from '../../assets/s2.mp4'
import s3 from '../../assets/s3.mp4'
import s4 from '../../assets/s4.mp4'
import s5 from '../../assets/s5.mp4'

const Divi = (props) => {
    return (
        <Container lg={3} xs={12} md={6}>
            <Col className=' d-flex justify-content-center '>
                <div style={{ backgroundColor: props.bg, borderRadius: '10px' }} className='p-4 w-lg-75'>


                    {props.no === 't1' ? <Reactplayer width='inherit' height='30vh' controls url={t1} /> : null}
                    {props.no === 't2' ? <Reactplayer width='inherit' height='30vh' controls url={t2} /> : null}

                    {props.no === 't3' ? <Reactplayer width='inherit' height='30vh' controls url={t3} /> : null}
                    {props.no === 's1' ? <Reactplayer width='inherit' height='30vh' controls url={s1} /> : null}
                    {props.no === 's2' ? <Reactplayer width='inherit' height='30vh' controls url={s2} /> : null}
                    {props.no === 's3' ? <Reactplayer width='inherit' height='30vh' controls url={s3} /> : null}
                    {props.no === 's4' ? <Reactplayer width='inherit' height='30vh' controls url={s4} /> : null}
                    {props.no === 's5' ? <Reactplayer width='inherit' height='30vh' controls url={s5} /> : null}


                </div>
            </Col>
        </Container>
    )
}

export default Divi