import React from 'react'
import './ticker.css'
import 'bootstrap/dist/css/bootstrap.min.css'
import { Button } from 'react-bootstrap'

const Ticker = () => {
    return (
        <section className='d-lg-flex  d-sm-none flex-column p-2'>
            <div className='Appo d-flex flex-row  flex-1'>
                <div className='onediv' style={{ backgroundColor: 'lightpink' }}>
                    1<sup>st</sup>
                </div>
                <div className='onediv' style={{ backgroundColor: 'lightsteelblue' }}>
                    2<sup>nd</sup>
                </div>
                <div className='onediv' style={{ backgroundColor: 'lightseagreen' }}>
                    3<sup>rd</sup>
                </div>
                <div className='onediv' style={{ backgroundColor: 'lightskyblue' }}>
                    4<sup>th</sup>
                </div>
                <div className='onediv' style={{ backgroundColor: 'lightslategrey' }}>
                    5<sup>th</sup>
                </div>
                <div className='onediv' style={{ backgroundColor: 'lightpink' }}>
                    6<sup>th</sup>
                </div>
                <div className='onediv' style={{ backgroundColor: 'lightgreen' }}>
                    7<sup>th</sup>
                </div>
                <div className='onediv' style={{ backgroundColor: 'lightblue' }}>
                    8<sup>th</sup>
                </div>
                <div className='onediv' style={{ backgroundColor: 'lightsalmon' }}>
                    9<sup>th</sup>
                </div>
                <div className='onediv' style={{ backgroundColor: 'lightgrey' }}>
                    10<sup>th</sup>
                </div>
                <div className='onediv' style={{ backgroundColor: 'lightcyan' }}>
                    11<sup>th</sup>
                </div>
                <div className='onediv' style={{ backgroundColor: 'lightseagreen' }}>
                    12<sup>th</sup>
                </div>

            </div>

        </section>
    )
}
export default Ticker